@use 'util/variables' as *;
@use 'util/fonts' as *;

.hero {
	height: 100vh;
	.container {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 3% 0;
		height: calc(100vh - 3rem);

		&__title {
			max-width: 25ch;
			font-size: 3rem;
			letter-spacing: 0.1rem;
			text-align: center;
			line-height: 1;
		}

		&__subtitle {
			max-width: 60ch;
			font-family: $font-heebo;
			font-weight: 400;
			color: $blue-gray-400;
			text-align: center;
			margin-bottom: 3rem;
		}

		&__buttons {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			gap: 1rem;

			.btn {
				padding: 0.5rem 1rem;
				border-radius: 0.5rem;
				font-size: 1.2rem;
				font-weight: 700;
				letter-spacing: 0.05rem;
				transition: all 0.5s ease;
			}
		}
	}
}
